<template>
	<div id="cm-page-outer">
		<div id="cm-toolpanel">
			<ToolSection title="Specify your problem">
				<div class="tp-entry">
					<span>Number of data classes {{nFeatures}}</span>
					<input type="range" v-model.number="nFeatures"
						min="2" max="8" step="1"/>
				</div>
				<div class="tp-entry">
					<span>Type of data</span>
					<select v-model="dataType">
						<option v-for="op in dataTypeOptions" :key="op">{{op}}</option>
					</select>
				</div>
				<div class="tp-entry">
					<span>Type of colour map</span>
					<select v-model="cmType">
						<option v-for="op in cmTypeOptions" :key="op">{{op}}</option>
					</select>
				</div>
			</ToolSection>
			<ToolSection title="Choose a colour map">
				<ColourmapList :maps="currentMaps" :nFeatures="nFeatures"
					@select="setSelectedCM"/>
			</ToolSection>
			<ToolSection title="Make it your own!">
				<div class="customise-row" v-if="selectedCM">
					<ColourmapCustomise :map="selectedCM" :nFeatures="nFeatures"
						@copy-colour="copyColour"
						@toggle-lock="i => selectedCM.toggleLock(i)"
						@remove-at="i => selectedCM.colours.splice(i,1)"
						@add-at="i => selectedCM.addAt(i)"
						@change-at="changeColourAt"
						:editable="cmType == 'Qualitative'"/>
					<ButtonMenu :map="selectedCM" @set-to="setMapColours"/>
				</div>
				<div class="middle-picker-outer">
					<ColorPicker v-show="cmType == 'Diverging'" label="Middle"
						ref="cpMid" @changed="val => changeMidColour(val,true)"/>
				</div>
			</ToolSection>
		</div>
		<div id="cm-preview"></div>
	</div>
</template>

<script>
import * as d3 from 'd3'
import exampleBarchart from './assets/exampleBarchart.json'
import exampleGraph from './assets/exampleGraph.json'
import exampleLinechart from './assets/exampleLinechart.json'
import {SequentialCM, DivergingCM, QualitativeCM, CustomisableCM} from './js/colours.js'
import {copyStringToClipboard} from './js/helpers.js'
import ColourmapList from './components/ColourmapList.vue'
import ColourmapCustomise from './components/ColourmapCustomise.vue'
import ButtonMenu from './components/ButtonMenu.vue'
import ToolSection from './components/ToolSection.vue'
import ColorPicker from './components/ColorPicker.vue'
import {minMax} from './js/helpers'
import space from 'color-space'
export default {
	name: 'App',
	components: {
		ColourmapList,
		ColourmapCustomise,
		ButtonMenu,
		ToolSection,
		ColorPicker
	},
	data () {
		return {
			nFeatures: 4,
			dataType: 'Bar Chart',
			dataTypeOptions: ['Bar Chart', 'Graph', 'Line Chart'],
			cmType: 'Sequential',
			cmTypeOptions: ['Sequential', 'Diverging', 'Qualitative'],
			midRGB: [255,255,255],
			sequentialMaps: [
				new SequentialCM([255,210,210],[255,30,30]),
				new SequentialCM([210,255,210],[30,255,30]),
				new SequentialCM([210,210,255],[30,30,255]),
				new SequentialCM([200,200,200],[30,30,30]),
				new SequentialCM([230,230,20],[180,30,180]),
				new SequentialCM([70,10,230],[130,210,30]),
			],
			divergingMaps: [
				new DivergingCM([255,0,0], [255,255,255], [0,255,0]),
				new DivergingCM([255,255,50],[255,255,255],[180,30,180]),
				new DivergingCM([30,30,210],[255,255,255],[210,30,30]),
				new DivergingCM([70,90,190],[255,255,255],[240,215,45]),
				new DivergingCM([255,140,30],[255,255,255],[0,255,0]),
				new DivergingCM([230,30,140],[255,255,255],[70,240,10])
			],
			qualitativeMaps: [
				new QualitativeCM(4),
				new QualitativeCM(4),
				new QualitativeCM(4),
				new QualitativeCM(4),
				new QualitativeCM(4),
				new QualitativeCM(4)
			],
			selectedCM: null,
			exampleDims: {
				margin: {top: 20, right: 20, bottom: 30, left: 40},
				width: 500,
				height: 500,
			},
			exampleElems: {x:null,y:null,svg:null}
		}
	},
	computed: {
		// dynamically updated attribute, returns list of maps based on current cmType
		currentMaps () {
			if (this.cmType == 'Sequential') return this.sequentialMaps
			else if (this.cmType == 'Diverging') return this.divergingMaps
			else return this.qualitativeMaps
		}
	},
	// called when the page is created, before mounted()
	created () {
		this.exampleDims.width -= this.exampleDims.margin.left - this.exampleDims.margin.right
		this.exampleDims.height -= this.exampleDims.margin.top - this.exampleDims.margin.bottom
	},
	// called when the page is created, after created()
	// in this method, can access and modify the DOM (unlike in created)
	mounted() {
		this.setSelectedCM(this.sequentialMaps[0], false)
		this.generateExample()
	},
	methods: {
		changeColourAt (index, hex) {
			this.selectedCM.changeAt(index, hex)
			this.updateExampleColours()
		},
		changeMidColour (value, updateMap) {
			this.midRGB = value
			this.$refs.cpMid.setFromRGB(value)
			if (updateMap) {
				this.selectedCM.mid = space.rgb.lab(value)
				this.updateExampleColours()
			}
		},
		generateExample () {
			var {margin,width,height} = this.exampleDims
			var oldSvg = d3.select("#cm-preview").select("svg")
			if (oldSvg) oldSvg.remove()
			var svg = d3.select("#cm-preview").append("svg")
				.attr("width", width + margin.left + margin.right)
				.attr("height", height + margin.top + margin.bottom)
				.append("g")
				.attr("transform", "translate(" + margin.left + "," + margin.top + ")");
			var data = this.generateData()
			var x, y;
			if (this.dataType == 'Bar Chart') {
				// set the ranges
				x = d3.scaleBand().range([0, width]).padding(0.1);
				y = d3.scaleLinear().range([height, 0]);
				// Scale the range of the data in the domains
				x.domain(data.map(function(d) { return d.city; }));
				y.domain([0, d3.max(data, function(d) { return d.value; })]);
				// append the rectangles for the bar chart
				svg.selectAll(".bar")
					.data(data)
					.enter().append("rect")
					.attr("class", "bar")
				this.$nextTick(() => {
					this.updateSelection(svg.selectAll(".bar").data(data))
				})
				// add the x Axis
				svg.append("g")
					.attr("id","x-axis")
					.attr("transform", "translate(0," + height + ")")
					.call(d3.axisBottom(x));
				// add the y Axis
				svg.append("g")
					.attr("id","y-axis")
					.call(d3.axisLeft(y));
			}
			else if (this.dataType == 'Graph') {
				// create edges
				svg.selectAll(".edge")
					.data(data.edges)
					.enter().append("line")
					.attr("class", "edge")
				// create nodes
				svg.selectAll(".node")
					.data(data.nodes)
					.enter().append("circle")
					.attr("r", function(d){return d.r})
					.attr("class", "node")
				this.$nextTick(() => {
					this.updateSelection([
						svg.selectAll(".edge").data(data.edges),
						svg.selectAll(".node").data(data.nodes)
					])
				})
			}
			else if (this.dataType == 'Line Chart') {
				// set the ranges
				x = d3.scaleLinear().range([0, width]);
				y = d3.scaleLinear().range([height, 0]);
				// Scale the range of the data in the domains
				x.domain([0,5]);
				y.domain([0,10]);
				// add the x Axis
				svg.append("g")
				.attr("id","x-axis")
				.attr("transform", "translate(0," + height + ")")
				.call(d3.axisBottom(x));
				// add the y Axis
				svg.append("g")
					.attr("id","y-axis")
					.call(d3.axisLeft(y));
				// d3 function for drawing line charts
				// calculates a <path> element's d attribute
				var valueline = d3.line()
					.curve(d3.curveBasis)
					.x(function(d) { return x(d.x); })
					.y(function(d) { return y(d.y); })
				// create containers for each line
				var lines = svg.selectAll('.line-container')
					.data(data)
					.enter()
					.append("g")
					.attr("class","line-container")
				lines.append("path")
					.attr("class","line")
					.style("stroke",function(d){return d.color})
					.attr("d",function(d){return valueline(d.data)})
				this.$nextTick(() => {
					this.updateSelection(svg.selectAll(".line-container").data(data))
				})
			}

			this.exampleElems.x = x
			this.exampleElems.y = y
			this.exampleElems.svg = svg
		},
		updateExampleColours () {
			var {svg} = this.exampleElems
			var data = this.generateData()
			if (this.dataType == 'Bar Chart') {
				this.updateSelection(svg.selectAll(".bar").data(data))
			}
			else if (this.dataType == 'Graph') {
				this.updateSelection([svg.selectAll(".edge").data(data.edges),svg.selectAll(".node").data(data.nodes)])
			}
			else if (this.dataType == 'Line Chart') {
				this.updateSelection(svg.selectAll(".line-container").data(data))
			}
		},
		// reads placeholder data from .json files
		// augments it with additional information such as colour
		// formats it into a format that visualisation methods expect (d3)
		generateData () {
			var colours = this.selectedCM.getColours(this.nFeatures, this.midRGB)
			var n = colours.length
			if (this.dataType == 'Bar Chart') {
				var genData = exampleBarchart.slice(0,n)
				var dataCopy = genData.map(gd => gd)
				if (this.cmType != 'Qualitative') {
					dataCopy.sort((a,b) => a.value - b.value)
				}
				var name2SortedIndex = {}
				for (var i = 0; i < n; i++) name2SortedIndex[dataCopy[i].city] = i
				genData.forEach(d => d.color = colours[name2SortedIndex[d.city]].hex)
				return genData
			}
			else if (this.dataType == 'Graph') {
				var g = {
					nodes: exampleGraph.nodes.slice(0,n),
					edges: exampleGraph.edges
				}
				g.edges = g.edges.filter(e => e.source < g.nodes.length && e.target < g.nodes.length)
				g.edges.forEach(e => {
					e.x1 = g.nodes[e.source].x
					e.y1 = g.nodes[e.source].y
					e.x2 = g.nodes[e.target].x
					e.y2 = g.nodes[e.target].y
				})
				if (this.cmType == 'Qualitative') {
					g.nodes.forEach((d,i) => d.color = colours[i].hex)
				}
				else {
					var {min,max} = minMax(g.nodes,(x) => x.r)
					g.nodes.forEach(d => {
						var fract = (d.r - min) / (max-min)
						var i = Math.floor(fract * colours.length)
						d.color = colours[Math.min(i,colours.length-1)].hex
					})
				}
				return g
			}
			else if (this.dataType == 'Line Chart') {
				var lch = []
				for (var j = 0; j < n; j++) lch.push(exampleLinechart[j])
				lch = lch.map((d,i) => {return {data: d, color: colours[i].hex}})
				return lch;
			}
		},
		// common method for using bound data to update visualisation
		// has behavior dependant on data type
		// defined so that code doesn't have to be repeated in first time build and subsequent updates
		updateSelection (update) {
			var {x,y} = this.exampleElems
			var {height} = this.exampleDims
			if (this.dataType == 'Bar Chart') {
				update.style("fill", function(d){return d.color})
					.attr("x", function(d) { return x(d.city); })
					.attr("width", x.bandwidth())
					.attr("y", function(d) { return y(d.value); })
					.attr("height", function(d) { return height - y(d.value); })
			}
			else if (this.dataType == 'Graph') {
				update[0]
					.attr("x1", function(d){return d.x1})
					.attr("y1", function(d){return d.y1})
					.attr("x2", function(d){return d.x2})
					.attr("y2", function(d){return d.y2})
				update[1]
					.attr("cx", function(d){return d.x})
					.attr("cy", function(d){return d.y})
					.style("fill",function(d){return d.color})
			}
			else if (this.dataType == 'Line Chart') {
				var valueline = d3.line()
					.curve(d3.curveBasis)
					.x(function(d) { return x(d.x); })
					.y(function(d) { return y(d.y); })
				update.select("path")
					.style("stroke",function(d){return d.color})
					.attr("d",function(d){return valueline(d.data)})
			}
		},
		copyColour (hex) {
			copyStringToClipboard(hex)
		},
		setSelectedCM (map, updateExample = true) {
			this.selectedCM = new CustomisableCM(map,this.nFeatures,this.cmType)
			if (this.cmType == 'Diverging') {
				this.changeMidColour(space.lab.rgb(map.mid),false)
			}
			if (updateExample) this.updateExampleColours()
		},
		// sets colour field with a list of L*a*b* colours
		// triggered from ButtonMenu either through undo or through generation
		setMapColours (colours) {
			this.selectedCM.colours = colours
			this.updateExampleColours()
		}
	},
	watch: {
		nFeatures: function () {
			if (this.cmType == 'Qualitative') {
				for (var i = 1; i < this.qualitativeMaps.length; i++) {
					this.qualitativeMaps[i] = new QualitativeCM(this.nFeatures)
				}
			}
			this.generateExample()
		},
		// on datatype change, update visualisation and colour map options
		dataType: function (val) {
			if (val == 'Bar Chart') this.cmTypeOptions = ['Sequential', 'Diverging', 'Qualitative']
			if (val == 'Graph') this.cmTypeOptions = ['Sequential', 'Diverging', 'Qualitative'] // can it be all of them? making assumptions
			if (val == 'Line Chart') this.cmTypeOptions = ['Qualitative']
			// change previous cmType if not in the new options
			if (!this.cmTypeOptions.find(o => o == this.cmType)) {
				this.cmType = this.cmTypeOptions[0]
			}
			this.generateExample()
		},
		cmType: function () {
			this.setSelectedCM(this.currentMaps[0])
			this.updateExampleColours()
		}
	}
}
</script>
