/* eslint-disable no-constant-condition */
/* eslint-disable no-redeclare */
import space from 'color-space'
import {dist,rand,colFloored,rgb2hex,hex2rgb,rgb2style} from './helpers.js'


// used for Sequential and Diverging colour maps
// interpolates between a starting and ending colour to get an intermediate colour
function getColourInRange(start, end, nSteps, i) {
	return [...Array(3).keys()].map(j => start[j] + (end[j]-start[j]) * (i / nSteps))
}

function getRandomLab(lMin = 30, lMax = 100) {
	return [rand(lMin,lMax),rand(-100,100),rand(-100,100)]
}

export class ColourMap {
	constructor() {
		if (this.getMap == undefined) throw new TypeError("Must override getMap method")
		if (this.getCopy == undefined) throw new TypeError("Must override getCopy method")
	}
}

export class SequentialCM extends ColourMap {
	// if no start and end are specified, generates a random start and end colour
	// if 3rd parameter (inputsInLab) is not specified / false, treats start/end as RGB
	// format of start / end (regardless of inputsInLab) is an array of 3 values
	// either [r,g,b] (going from 0 to 255) or [L*,a*,b*]
	constructor(start, end, inputsInLab) {
		super()
		// generate a random start and end
		if (!start || !end) {
			var minDist = 80
			var maxDist = 180
			var labStart = getRandomLab() 
			var labEnd;
			while (true) {
				labEnd = getRandomLab()
				var d = dist(labStart,labEnd)
				if (d > minDist && d < maxDist) break
			}
			this.start = labStart
			this.end = labEnd
		}
		// inputs in lab, everything ok
		else if (inputsInLab) {
			this.start = start
			this.end = end
		}
		// convert from rgb to lab
		else {
			this.start = space.rgb.lab(start)
			this.end = space.rgb.lab(end)
		}
	}
	// if getFormatted is true, returns {rgb, style, hex}, meant to be used in UI
	// otherwise returns array of L*a*b* colours (each is [L*,a*,b*])
	// uses getColourInRange to interpolate between this.start and this.end
	getMap(n, getFormatted = true) {
		var cols = []
		for (var i = 0; i < n; i++) {
			var labVersion = getColourInRange(this.start,this.end,n-1,i)
			if (!getFormatted) {
				cols.push(labVersion)
			}
			else {
				var rgbVersion = space.lab.rgb(labVersion).map(v => Math.round(v))
				cols.push({rgb: rgbVersion, style: rgb2style(rgbVersion), hex: rgb2hex(rgbVersion)})
			}
		}
		return cols
	}
	getCopy() {
		return new SequentialCM(this.start, this.end)
	}
}

export class DivergingCM extends ColourMap {
	// if no start, mid, and end are specified, generates random start and end, and mid = white
	// if 4th parameter (inputsInLab) is not specified / false, treats provided as RGB
	// format of start / mid / end (regardless of inputsInLab) is an array of 3 values
	// either [r,g,b] (going from 0 to 255) or [L*,a*,b*]
	constructor(start,mid,end,inputsInLab) {
		super()
		if (!start) this.start = getRandomLab(30,60)
		else if (inputsInLab) this.start = start
		else this.start = space.rgb.lab(start)
		if (!mid) this.mid = space.rgb.lab([255,255,255])
		else if (inputsInLab) this.mid = mid
		else this.mid = space.rgb.lab(mid)
		if (!end) this.end = getRandomLab(30,60)
		else if (inputsInLab) this.end = end
		else this.end = space.rgb.lab(end)
	}
	// if getFormatted is true, returns {rgb, style, hex}, meant to be used in UI
	// otherwise returns array of L*a*b* colours (each is [L*,a*,b*])
	// uses getColourInRange to interpolate between this.start, this.mid, and this.end
	getMap(n, getFormatted = true) {
		var cols = []
		var halfN = Math.floor(n / 2)
		for (var i = 0; i < n; i++) {
			var labVersion = null
			if (n % 2 == 1 && i == halfN) labVersion = this.mid
			else if (i < halfN) labVersion = getColourInRange(this.start,this.mid,halfN,i)
			else {
				var idx = i - halfN + (n % 2 == 1 ? 0 : 1)
				labVersion = getColourInRange(this.mid,this.end,halfN,idx)
			}
			if (!getFormatted) {
				cols.push(labVersion)
				continue
			}
			var rgbVersion = space.lab.rgb(labVersion).map(v => Math.round(v))
			cols.push({rgb: rgbVersion, style: rgb2style(rgbVersion), hex: rgb2hex(rgbVersion)})
		}
		return cols
	}
	getCopy() {
		return new DivergingCM(this.start, this.mid, this.end)
	}
}

/* 
 * Generates a qualitative colour map, given certain constraints
 * User can specify the number of colours, the indices where colours are locked, and an initial colour map
 * Only the firt parameter (nColours) is required
 * This method uses a genetic algorithm. The number of iterations can be specified with the 4th parameter (default is 200)
 */
function generateQualitativeMap(nColours, lockedIndices, initialMap = null, iterations = 100) {
	if (!lockedIndices) lockedIndices = new Set()
	// calculate an allowed range for L*
	// this is 55-80 if no initial map is given
	// otherwise it is calculated based on initialMap, with a minimum difference of minRange
	var lMin, lMax;
	if (lockedIndices.size == 0 || initialMap == null) {
		lMin = 40
		lMax = 90
	}
	else {
		lMin = 100
		lMax = 0
		const minRange = 30
		lockedIndices.forEach(i => {
			if (initialMap[i][0] < lMin) lMin = initialMap[i][0]
			if (initialMap[i][0] > lMax) lMax = initialMap[i][0]
		})
		if (lMax - lMin < minRange) {
			var missingRange = minRange - (lMax - lMin)
			// if possible, extend the same amount in both directions
			if (lMin > missingRange / 2 && lMax < 100 - missingRange / 2) {
				lMin -= missingRange / 2
				lMax += missingRange / 2
			}
			// otherwise, extend to lighter colours as much as possible, rest to darker
			else {
				var extendToLight = Math.min(missingRange, 100 - lMax)
				lMax = 100
				lMin -= extendToLight - missingRange
			}
		}
	}
	// constructs a starting map
	// this is a partial or complete clone of initialMap, if provided and based on lockedIndices
	// otherwise it starts with random L*a*b* values (respecting lMin and lMax)
	var startMap = []
	for (var i = 0; i < nColours; i++) {
		// have to make a clone of each colour value, otherwise VueJS watchers are triggered
		if (initialMap && lockedIndices.has(i)) startMap.push(initialMap[i].map(c => c))
		else startMap.push(getRandomLab(lMin,lMax))
	}
	var mutateRange = 7
	// mutates a single colour (array of 3 values [L*,a*,b*])
	function mutate(col) {
		var newCol = []
		for (var i = 0; i < 3; i++) {
			newCol.push(col[i] + rand(-mutateRange,mutateRange))
			if (i == 0) {
				if (newCol[i] < lMin) newCol[i] = lMin
				else if (newCol[i] > lMax) newCol[i] = lMax
			}
			else {
				if (newCol[i] < -100) newCol[i] = -100
				else if (newCol[i] > 100) newCol[i] = 100
			}
		}
		return newCol
	}
	// mutates a colour map (array of nColours colours, each in [L*,a*,b*] format)
	function mutateMap(map) {
		var newMap = []
		for (var i = 0; i < nColours; i++) {
			if (lockedIndices.has(i)) newMap.push(map[i])
			else newMap.push(mutate(map[i]))
		}
		return newMap
	}
	// fitness function for the genetic algorithm
	// takes a colour map as input and scores it, with higher scores being better
	// in this case, it is maximising the smallest pair-wise distance
	function score(map) {
		var distMin = 10000
		for (var i = 0; i < nColours - 1; i++) {
			for (var j = i + 1; j < nColours; j++) {
				var d = dist(map[i],map[j])
				if (d < distMin) distMin = d
			}
		}
		return distMin
	}
	var mutations = []
	for (var i = 0; i < 100; i++) mutations.push(mutateMap(startMap, false))
	mutations.sort((a,b) => score(b) - score(a))
	for (var iter = 0; iter < iterations; iter++) {
		// mutate every map but the top 10
		for (var i = 10; i < mutations.length; i++) {
			mutations[i] = mutateMap(mutations[i%10], false)
			// mutateMap(mutations[i],true)
		}
		mutations.sort((a,b) => score(b) - score(a))
	}
	return mutations[0]
}

export class QualitativeCM extends ColourMap {
	constructor(n) {
		super()
		this.colours = generateQualitativeMap(n)
	}
	// if getFormatted is true, returns {rgb, style, hex}, meant to be used in UI
	// otherwise returns array of L*a*b* colours (each is [L*,a*,b*])
	getMap(n, getFormatted = true) {
		return n <= this.colours.length ? this.colours.slice(0,n).map(c => {
			if (!getFormatted) return c
			var rgbVersion = colFloored(space.lab.rgb(c))
			return {rgb: rgbVersion, style: rgb2style(rgbVersion), hex: rgb2hex(rgbVersion)}
		}) : null
	}
	getCopy() {
		return new QualitativeCM(this.colours)
	}
}

export class CustomisableCM extends QualitativeCM {
	constructor(originalMap, n, mapType) {
		super()
		this.locked = new Set() // set of locked indices
		this.colours = originalMap.getMap(n,false)
		this.mapType = mapType
	}
	// gets a list of {rgb, style, hex} colour objects to be used by the UI
	// required first parameter nFeatures
	// optional second parameter midRGB, in case the map type is diverging
	getColours (nFeatures, midRGB) {
		if (nFeatures) {
			if (this.mapType == 'Sequential') {
				this.colours = (new SequentialCM(this.colours[0], this.colours[this.colours.length-1], true)).getMap(nFeatures, false)
			}
			else if (this.mapType == 'Diverging') {
				this.colours = (new DivergingCM(this.colours[0], space.rgb.lab(midRGB), this.colours[this.colours.length-1], true)).getMap(nFeatures, false)
			}
		}
		return this.colours.map(c => {
			var rgbVersion = colFloored(space.lab.rgb(c))
			return {rgb: rgbVersion, style: rgb2style(rgbVersion), hex: rgb2hex(rgbVersion)}
		})
	}
	toggleLock(index) {
		if (this.locked.has(index)) this.locked.delete(index)
		else this.locked.add(index)
	}
	// inserts a new colour at a given index
	// uses the generateQualitativeMap method to get a good colour (locking all indices but the new one)
	addAt(index) {
		var newColours = this.colours.map(c => c)
		// insert random new colour
		newColours.splice(index,0,[rand(50,100),rand(-100,100),rand(-100,100)])
		// use optimisation, with all existing colours locked except the new one
		var tempLocked = new Set()
		for (var i = 0; i < newColours.length; i++) {
			if (i != index) tempLocked.add(i)
		}
		this.colours = generateQualitativeMap(newColours.length,tempLocked,newColours)
	}
	changeAt(index, hex) {
		this.colours[index] = space.rgb.lab(hex2rgb(hex))
	}
	// generates a new colour map, given current constraints, and returns it
	// used in components/ButtonMenu.vue when user presses the generate button
	generateNew() {
		return generateQualitativeMap(this.colours.length,this.locked,this.colours)
	}
	getColoursCopy() {
		return this.colours.map(c => c)
	}
}

