<template>
	<div class="btn-menu-outer">
		<i v-if="map.mapType == 'Qualitative'" class="material-icons unselectable btn" :class="{'unavailable': previous.length == 0}" @click="undo">undo</i>
		<i v-if="map.mapType == 'Qualitative'" class="material-icons unselectable btn" @click="generate">palette</i>
		<i class="material-icons unselectable btn" @click="download">download</i>
	</div>
</template>
<script>
import JSZip from 'jszip'
import { saveAs } from 'file-saver';
export default {
	props: {
		map: Object
	},
	data () {
		return {
			// stores previous colour maps for undo functionality
			previous: []
		} 
	},
	methods: {
		undo () {
			if (this.previous.length == 0) return
			var last = this.previous.pop()
			this.$emit('set-to',last)
		},
		generate () {
			this.previous.push(this.map.getColoursCopy())
			var newColours = this.map.generateNew()
			this.$emit('set-to',newColours)
		},
		download () {
			var zip = new JSZip()
			var cols = this.map.getColours()
			var objToRelevantData = (c) => {return {rgb: c.rgb, hex: c.hex}}
			// JSON array of values
			zip.file("colourmap-array.json", JSON.stringify(cols.map(c => objToRelevantData(c))));
			// JSON key-value mapping
			var obj = {}
			for (var i = 0; i < cols.length; i++) obj[`color${i+1}`] = objToRelevantData(cols[i])
			zip.file("colourmap-obj.json",JSON.stringify(obj))
			// css stylesheet
			zip.file("colourmap-styles.css",cols.map((c,i) => `.col-${i+1} {\n\tcolor:${c.hex};\n}\n.bg-${i+1} {\n\tbackground:${c.hex};\n}\n`).join(''))
			// scss variables
			zip.file("colourmap-vars.scss",cols.map((c,i) => `$color-${i+1}: ${c.hex};\n`).join(''))
			zip.generateAsync({type:"blob"})
				.then(function (blob) {
					saveAs(blob, "colourmap-files.zip");
				});
		}
	}
}
</script>
<style lang="scss">
.btn-menu-outer {
	width:50px;
	display:flex;
	flex-direction:column;
	justify-content:center;
	align-items:center;
	i {
		margin: 5px;
	}
	i.unavailable {
		opacity: 0.3;
		cursor:default;
	}
	i.unavailable:hover {
		opacity: 0.3;
	}
}
</style>

