<template>
	<div class="cm-selection">
		<div class="cm-col">
			<div v-for="map in maps.slice(0,half)" :key="map"
				class="colourmap-preview" @click="$emit('select',map)">
				<div v-for="c in map.getMap(nFeatures)" :key="`${map}-${c}`"
					class="colourmap-part" :style="`background:${c.style}`"/>
			</div>
		</div>
		<div class="cm-col">
			<div v-for="map in maps.slice(half)" :key="map"
				class="colourmap-preview" @click="$emit('select',map)">
				<div v-for="c in map.getMap(nFeatures)" :key="`${map}-${c}`"
					class="colourmap-part" :style="`background:${c.style}`"/>
			</div>
		</div>
	</div>
</template>
<script>
export default {
	props: {
		maps: Array,
		nFeatures: Number
	},
	computed: {
		half () {
			return Math.ceil(this.maps.length / 2)
		}
	}
}
</script>
