<template>
	<div id="sel-cm-preview" @mouseleave="hoverIndex = -1">
		<input type="color" :onchange="onInputChange" ref="changeColourInput"/>
		<div v-for="el in elems"
			:key="el"
			:class="{'sel-cm-part': el.type == 'col', 'add-btn': el.type == 'add', 'expanded': el.type == 'col' && hoverIndex == el.index}"
			:style="el.type == 'col' ? `background:${el.colour.hex};color:${hoverIndex != el.index ? '#0000' : fontColour(el.colour.rgb)}` : ''"
			@mouseenter="mouseEnterPart(el)">
			<div class="fr fjc" v-if="el.type == 'col'">
				<i v-if="mapType == 'Qualitative'"
					class="material-icons unselectable btn"
					@click="$emit('remove-at',el.index)">
					cancel
				</i>
				<i v-if="mapType == 'Qualitative'"
					class="material-icons unselectable btn"
					@click="$emit('toggle-lock',el.index)">
					{{map.locked.has(el.index) ? 'lock_outline' : 'lock_open'}}
				</i>
				<i class="material-icons unselectable btn"
					@click="$emit('copy-colour',el.colour.hex)">
					content_copy
				</i>
			</div>
			<div v-if="el.type == 'col' && hoverIndex == el.index"
				class="part-hex unselectable"
				:class="{'hex-clickable': el.editable}"
				@click="changeColourAt(el.index, el.colour.hex)">
				{{el.colour.hex}}
			</div>
			<i v-if="el.type == 'add' && mapType == 'Qualitative'"
				class="material-icons unselectable btn"
				@click="$emit('add-at',el.index)">
				add_circle
			</i>
		</div>
	</div>
</template>
<script>
export default {
	props: {
		nFeatures: Number,
		map: Object
	},
	data () {
		return {
			hoverIndex: 1,
			changingIndex: -1
		}
	},
	methods: {
		// handle mouseenter events and set hoverIndex if necessary
		mouseEnterPart (part) {
			if (part.type == 'col') this.hoverIndex = part.index
		},
		// check if colour is editable based in the index & map type
		// for example all colours are editable in Qualitative
		// but only start/end are in Sequential, and only start/end/mid in Diverging
		colourIsEditable (index) {
			var len = this.map.colours.length
			if (this.map.mapType == 'Qualitative') return true
			if (index == 0 || index == len - 1) return true
			if (this.map.mapType == 'Diverging' && len % 2 == 1 && index * 2 + 1 == len) return true
			return false
		},
		// make font of hex / buttons black or white depending on background
		fontColour (backgroundColour) {
			var sum = backgroundColour.reduce((a,b) => a + b)
			if (sum > 255 * 1.5) return 'black'
			else return 'white'
		},
		// user clicked colour hex
		// if allowed, open native colour picker widget
		changeColourAt (index, originalHex) {
			if (!this.colourIsEditable(index)) return
			this.changingIndex = index
			this.$refs.changeColourInput.value = originalHex
			this.$refs.changeColourInput.click()
		},
		// user submitted colour with colour picker widget
		// notify parent with the changed index and new colour (hex)
		onInputChange (evt) {
			this.$emit('change-at', this.changingIndex, evt.target.value.toUpperCase())
		}
	},
	computed: {
		// alternating sequence of colour map parts and add buttons between them
		elems () {
			var cols = this.map.getColours()
			if (!this.map.colours) return []
			var e = []
			if (this.map.mapType == 'Qualitative') {
				e.push({type: 'add', index: 0})
			}
			cols.forEach((c,i) => {
				e.push({type: 'col', index: i, colour: c, editable: this.colourIsEditable(i)})
				// add buttons only added if map type is Qualitative
				if (this.map.mapType == 'Qualitative') {
					e.push({type: 'add', index: i+1})
				}
			})
			return e
		},
		// shortcut for mapType, used in v-if's in <template>
		mapType () {
			return this.map.mapType
		}
	}
}
</script>
<style lang="scss">
#sel-cm-preview {
	display:flex;
	flex-direction:row;
	height:120px;
	margin-left: 10px;
	background:red;
	input[type="color"] {
		display: none;
	}
	.sel-cm-part {
		color: #0000;
		text-align:center;
		height: 100%;
		flex: 1;
		color:white;
		transition: flex 0.1s linear 0s;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: flex-end;
		padding: 0px 10px;
		.part-hex {
			padding: 10px 0px;
			font-size:22px;
			&.hex-clickable {
				cursor: pointer;
				text-decoration: underline;
			}
		}
		i {
			display:none;
			margin-top: 7px;
		}
	}
	.sel-cm-part.expanded {
		flex:3;
		i {
			display: inline-block;
		}
	}
	.add-btn {
		width: 40px;
		margin-right:-20px;
		margin-left:-20px;
		display:flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;
		z-index:1;
		i {
			display:none;
			cursor: pointer;
			font-size: 30px!important;
			color: #eee;
		}
	}
	.add-btn:hover {
		i {
		display:inline-block;
		text-shadow: 0 0 3px black;
		}
	}
	.material-icons.btn {
		margin: 0px 7px;
	}
}

</style>

