<template>
	<div class="color-picker">
		<div v-if="label" class="cp-label">
			{{label}}
		</div>
		<div class="cp-preview" :style="`background:${hex}`"
			@click="$refs.colInput.click()"/>
		<input type="color" :value="hex" :onchange="onInputChange" ref="colInput"/>
		<div class="cp-hex">
			{{hex}}
		</div>
	</div>
</template>
<script>
import {hex2rgb,rgb2hex,colFloored} from '../js/helpers'
export default {
	props: {
		label: {
			type: String,
			default: null
		}
	},
	data () {
		return {
			hex: '#ff0000'
		}
	},
	methods: {
		onInputChange (evt) {
			this.hex = evt.target.value.toUpperCase()
			this.$emit('changed', hex2rgb(this.hex))
		},
		setFromRGB (rgb) {
			this.hex = rgb2hex(colFloored(rgb))
		}
	}
}
</script>
