export function copyStringToClipboard (str) {
	// Create new element
	var el = document.createElement('textarea');
	// Set value (string to be copied)
	el.value = str;
	// Set non-editable to avoid focus and move outside of view
	el.setAttribute('readonly', '');
	el.style = {position: 'absolute', left: '-9999px'};
	document.body.appendChild(el);
	// Select text inside element
	el.select();
	// Copy text to clipboard
	document.execCommand('copy');
	// Remove temporary element
	document.body.removeChild(el);
}

export function arrEquals(arr1,arr2) {
	if (!arr1 || !arr2) return false
	if (arr1.length != arr2.length) return false
	for (var i = 0; i < arr1.length; i++) if (arr1[i] != arr2[i]) return false
	return true
}
export function rand(start,end) {
	if (!end) {
		end = start
		start = 0
	}
	return start + Math.floor(Math.random()*(end-start))
}
export function minMax(arr,accessor = (x) => x) {
	if (arr.length == 0) return {min:0,max:0}
	var min = accessor(arr[0]), max = accessor(arr[0])
	for (var i = 1; i < arr.length; i++) {
		var val = accessor(arr[i])
		if (val < min) min = val
		if (val > max) max = val
	}
	return {min,max}
}

export function dist(col1,col2) {
	return Math.sqrt(Math.pow(col1[0]-col2[0],2) + Math.pow(col1[1]-col2[1],2) + Math.pow(col1[2]-col2[2],2))
}

export function colFloored(col) {
	return [Math.floor(col[0]),Math.floor(col[1]),Math.floor(col[2])]
}

export function hex2rgb(hex) {
	return [
		+("0x" + hex.slice(1,3)),
		+("0x" + hex.slice(3,5)),
		+("0x" + hex.slice(5,7))
	]
}
function component2hex(c) {
	var hex = c.toString(16);
	return hex.length == 1 ? "0" + hex : hex;
}
export function rgb2hex(arr) {
	return ("#" + component2hex(arr[0]) + component2hex(arr[1]) + component2hex(arr[2])).toUpperCase();
}

export function rgb2style (arr) {
	return `rgb(${arr[0]},${arr[1]},${arr[2]})`
}

